import { Component, OnInit } from '@angular/core';
import { PermissionsService } from '../../services/permissions/permissions.services';
import { RolesServices } from '../../services/roles/roles.services';

export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  symbol: string;
}


@Component({
  selector: 'app-permissions',
  templateUrl: './permissions.component.html',
  styleUrls: ['./permissions.component.css']
})
export class PermissionsComponent implements OnInit {

  constructor(private permissionService: PermissionsService, private roleServices: RolesServices) {
  }


  permissionsData = [];
  rolesData = [];
  rolesPermission = [];

  ngOnInit() {
    this.getPermissions();
    this.getRoles();
    this.rolePermissions();
  }

  getPermissions() {
    this.permissionService.getPermissions().subscribe(res => {
      this.permissionsData = res['data'];
    }, error => {
    });
  }

  getRoles() {
    this.roleServices.getRoles().subscribe(res => {
      this.rolesData = res['roles'];
    }, error => {
    });
  }

  togglePermission(permissionId, roleId) {
    const data = {
      role_id: roleId,
      permission_id: permissionId
    };
    this.permissionService.togglePermission(data).subscribe(res => {
    });
  }

  rolePermissions() {
    this.permissionService.getRolesPermissions().subscribe(res => {
      this.rolesPermission = res['data'];
    });
  }

  check(roleId, permissionId) {
    let obj = this.rolesPermission.find(
      obj => (obj.role_id === roleId
        &&
        obj.permission_id === permissionId)
    );
    if (obj) {
      return true;
    }
    return false;
  }

}
